.add-cell {
  position: relative;
  opacity: 0;
  transition: opacity 0.3s ease-in 0.1s;
  margin: 20px 0 10px 0;
}

.add-cell:hover {
  opacity: 1;
}

.add-cell .divider {
  position: absolute;
  top: 50%;
  bottom: 50%;
  right: 5.5%;
  left: 2.5%;
  border-bottom: 1px dashed grey;
  width: 90%;
  z-index: -1;
}

.add-cell .add-buttons {
  display: flex;
  justify-content: center;
}

.add-cell .add-buttons button {
  margin: 0 20px;
  transition: 0.3s;
}

.add-cell .add-buttons button:active {
  background-color: darkblue;
  transform: scale(0.9);
}

.add-cell.visible {
  opacity: 1;
}
